import { Form, Input, Select } from 'antd';
import { Img } from 'assets/img';
import PrimaryButton from 'components/Common/PrimaryButton';
import { useFormik } from 'formik';
import { useTranslation } from 'react-i18next';

const { Option } = Select;

const initialValues = {
  companyName: '',
  bank: '',
  contactName: '',
  phoneNumber: '',
  position: '',
  email: '',
};

function RegisterForm() {
  const { t } = useTranslation();

  const formik = useFormik({
    initialValues,
    onSubmit: () => {},
  });

  return (
    <div className="register">
      <div className="container">
        <h3 className="register__title">
          {t('register.Đăng ký trở thành đối tác của Fundiin')}
        </h3>
        <p className="register__subtitle">
          {t(
            'register.Để lại thông tin và tìm hiểu cơ hội hợp tác cùng chúng tôi ngay hôm nay!'
          )}
        </p>
      </div>
      <div className="container">
        <div className="register__form">
          <Form
            labelCol={{ span: 24 }}
            wrapperCol={{ span: 24 }}
            onFinish={formik.handleSubmit}
          >
            <Form.Item
              name="companyName"
              rules={[{ required: true }]}
              label={t('register.Tên doanh nghiệp')}
            >
              <Input
                size="large"
                placeholder={t('register.Nhập tên doanh nghiệp')}
                onChange={value => formik.setFieldValue('companyName', value)}
              />
            </Form.Item>
            <Form.Item
              name="goods"
              rules={[{ required: true }]}
              label={t('register.Ngành hàng')}
            >
              <Select
                size="large"
                placeholder={t('register.Chọn ngành hàng')}
                onChange={value => formik.setFieldValue('goods', value)}
                allowClear
              >
                <Option value="1">Ngành hàng 1</Option>
                <Option value="2">Ngành hàng 1</Option>
                <Option value="3">Ngành hàng 1</Option>
              </Select>
            </Form.Item>
            <Form.Item
              name="phoneNumber"
              rules={[{ required: true }]}
              label={t('register.Số điện thoại liên hệ')}
            >
              <Input
                size="large"
                placeholder={t('register.Nhập số điện thoại')}
                onChange={value => formik.setFieldValue('phoneNumber', value)}
              />
            </Form.Item>
            <Form.Item
              name="contactName"
              rules={[{ required: true }]}
              label={t('register.Tên người liên hệ')}
            >
              <Input
                size="large"
                placeholder={t('register.Nhập họ và tên')}
                onChange={value => formik.setFieldValue('contactName', value)}
              />
            </Form.Item>
            <Form.Item
              name="email"
              rules={[{ required: true }]}
              label={t('register.Email')}
            >
              <Input
                size="large"
                placeholder={t('register.Nhập email')}
                onChange={value => formik.setFieldValue('email', value)}
              />
            </Form.Item>
            <Form.Item
              name="position"
              rules={[{ required: true }]}
              label={t('register.Chức danh')}
            >
              <Input
                size="large"
                placeholder={t('register.Nhập chức danh')}
                onChange={value => formik.setFieldValue('position', value)}
              />
            </Form.Item>

            <PrimaryButton type="submit" className="register-btn">
              {t('Gửi')}
            </PrimaryButton>
          </Form>
        </div>
      </div>
      <div className="register__bg">
        <img src={Img.registerBgUrl} alt="register bg" />
      </div>
    </div>
  );
}

export default RegisterForm;
